*{
  margin: 0px;
  padding: 0px;
}
.body {
  font-family: Franklin Gothic Medium;
  background-color: #0081A7;
  min-height: 100vh;
  font-size: 14pt;
  display: flex;
  flex-direction: column;
}
ul{
  display: flex;
  z-index: 1;
  height: max-content;
}
.navbar{
  width: 100%;
  height: max-content;
  z-index: 1;
  background-color: rgba(254, 217, 183, 0.3);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* background-color: red; */
}

ul li a {
  position: relative;
  z-index: 1;
  transition: color 0.1s;
}

ul li{
  padding: 10px;
  position: relative;
  list-style: none;
  cursor: pointer;
  color: white;
  z-index: 1;
  margin: 0px 10px;
  font-weight: bold;
  transition: color 0.2s;
  text-align: center;
  /* background: green; */
}

ul li:before {
  content: "";
  background-color: #FDFCDC;
  position: absolute;
  border-radius: 0px 0px 10px 10px;
  top: 0px;
  z-index: 1;
  left: 0px;
  width: 100%;
  height: 10%;
  transition: height 0.2s;
}
ul li:hover:before{
  height: 100%;
}
ul li:hover{
  color: #0081A7;
}

/* icon and discord social */
.discord{
  color: white;
  padding: 10px 20px;
  justify-content: center;
  /* background-color: red; */
  color: white;
  cursor: pointer;
  transition: color 0.2s;
  z-index: 1;
  display: flex;
  position: relative;
  font-weight: bold;
}
.discord:before{
  content: "";
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: -1;
  transition: width 0.2s, background-color 0.2s;
  height: 100%;
  width: 5%;
  border-radius: 10px 0px 0px 10px;
  position: absolute;
  background-color: #FDFCDC;
}
.discord:hover:before{
  width: 100%;
  background-color: #1e2124;
}
.discord:hover{
  color: #7289da;
}

.icon{
  color: white;
  padding: 10px 20px;
  justify-content: center;
  /* background-color: red; */
  color: white;
  cursor: pointer;
  transition: color 0.2s;
  z-index: 1;
  display: flex;
  position: relative;
  font-weight: bold;
}
.icon:before{
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: -1;
  transition: width 0.2s, background-color 0.2s;
  height: 100%;
  width: 5%;
  border-radius: 0px 10px 10px 0px;
  position: absolute;
  background-color: #FDFCDC;
}
.icon:hover:before{
  width: 100%;
  background-color: #405DE6;
}
.icon:hover{
  color: white;
}

/* This content under maintenance */
.maintenance{
  text-align: center;
  margin-top: 10vh;
  line-height: 5vh;
  color: white;
}

.loading-anim{
  height: 20px;
  margin: 5vh 10vh;
  transform-origin: center;
  display: flex;
  z-index: 1;
  border: solid white 2px;
  background-color: #0081A7;
  position: relative;
  border-radius: 50px;
  /* background-color: red; */
}

.loading-anim:before{
  content: "";
  z-index: -1;
  width: 50px;
  border-radius: 50px;
  transform-origin: center;
  display: flex;
  position: absolute;
  animation: progress 2s infinite;
  height: 20px;
  background-color: #FDFCDC;
}

@keyframes progress{
  0%{left: 50%;}
  25%{left: 75%;}
  50%{left: 25%;}
  75%{left: 50%;}
  100%{left: 50%;}
}